declare var $: any
import { Component } from '@angular/core';
import { Events, IonicPage, NavController, NavParams, ToastController } from 'ionic-angular';
import { HttpServicesProvider } from '../../../providers/http-services';
import { PhoneDeviceProvider } from '../../../providers/phone-device';
import { StorageProvider } from '../../../providers/storage';
import { ToolsProvider } from '../../../providers/tools';
import { Utils } from '../../../providers/Utils';
import { WorkflowServiceProvider } from '../../../providers/workflow-service';


/**
 * Generated class for the LmtagprojectlogPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-lmtagprojectlog',
  templateUrl: 'lmtagprojectlog.html',
})
export class LmtagprojectlogPage {

  public formdata = [
    { 'name': '检修任务', 'index': 'projectname', 'indexId': 'prjid', 'type': 'chooseitemname', 'placeholder': '请输入检修任务' },
    { 'name': '主要工作内容', 'index': 'projectdesc', 'type': 'textarea', 'placeholder': '请输入工作内容', 'disabled': true, 'autoheight': true },
  ]
  public formdata2 = [
    { 'name': '入场时间', 'index': 'entertime', 'type': 'datetime', 'format': 'YYYY-MM-DD HH:mm:ss', 'placeholder': '请选择入场时间' },
    { 'name': '出场时间', 'index': 'leavetime', 'type': 'datetime', 'format': 'YYYY-MM-DD HH:mm:ss', 'placeholder': '请选择出场时间' },
    { 'name': '当前登记类型', 'index': 'edittype', 'type': 'select', 'dicttype': 'LMTAG_PROJECT_LOG_EDITTYPE', 'placeholder': '请选择当前登记类型' },
  ]
  public formdata3 = [
    { 'name': '备注', 'index': 'remark', 'type': 'textarea', 'placeholder': '请填写备注' },
  ]
  public formdata4 = [
    { 'name': '今日进展', 'index': 'todayprogress', 'type': 'textarea', 'placeholder': '请填写今日进展' },
    { 'name': '附件', 'index': 'logid', 'type': 'uploadify', 'placeholder': '上传附件', 'busstype': 'lmtagProjectLog' },
    { 'name': '明日计划', 'index': 'tomorrowplan', 'type': 'textarea', 'placeholder': '请填写明日计划' },
  ]

  public id = "";
  public prjid = '';
  public forminfo = {};
  public projectid = '';
  public type = '';
  public projectinfo = {};
  public toollist = [];
  public pageControl = {};

  constructor(public events: Events, public storage: StorageProvider, public navCtrl: NavController, public toastCtrl: ToastController, public navParams: NavParams, public httpService: HttpServicesProvider, public workflow: WorkflowServiceProvider,
    public tools: ToolsProvider,
    public phoneDevice: PhoneDeviceProvider,
    public toast: ToastController) {
    this.phoneDevice.phoneBacktoPop(this.navCtrl);

    this.type = this.navParams.get('type');
    this.id = this.navParams.get('logid');
    this.prjid = this.navParams.get('prjid');

    console.log(this.type, this.id, this.prjid);
    
    if (this.type == 'edit') {
      // 日志信息
      this.initinfo();
    } else {
      // 新增
      this.id = Utils.getUuid() // 日志id
      this.forminfo = {
        logid: this.id,
        prjid: this.prjid,
        entertime: Utils.dateFormat(new Date(), 'yyyy-MM-dd hh:mm:ss'),
        leavetime: '',
        enterpersonready: 0,
        leavepersonready: 0,
        enterperson: 1,
        leaveperson: 1,
        edittype: '1',
        todayprogress: '',
        tomorrowplan: '',
      }
      if (this.prjid != '' && this.prjid != null) {
        this.initprojectinfo(this.prjid);
        this.addlog(); // 预创建日志
      }
      this.httpService.inittime('entertime', this.forminfo);
    }
  }

  ionViewWillEnter() {
  }

  addlog() {
    var url = '/lmtagProjectLog/addLog?prjid=' + this.prjid + '&logid=' + this.id
    this.httpService.get(url, null, result => {
      if (result.code == 0) {
        this.inittoollist();
      }
    })
  }

  initinfo() {
    var url = "/lmtagProjectLog/detail/" + this.id;
    this.httpService.get(url, null, (result) => {
      // console.log(result)
      if (result.code == 0) {
        this.forminfo = result.data;
        this.prjid = result.data['prjid'];

        this.httpService.inittime('entertime', this.forminfo);
        this.httpService.inittime('leavetime', this.forminfo);

        this.forminfo['enterperson'] == null && (this.forminfo['enterperson'] = 0)
        this.forminfo['leaveperson'] == null && (this.forminfo['leaveperson'] = 0)

        this.pageElement(result.data.wfstatus, result.data.wfid);
        this.inittoollist();
        this.initprojectinfo(this.prjid);
      }
    })
  }

  initprojectinfo(id) {
    this.httpService.get('/lmtagProject/detail/' + id, null, (result) => {
      if (result.code == 0) {
        this.forminfo['projectdesc'] = result.data['projectdesc'];
        this.forminfo['projectname'] = result.data['projectname'];
        this.forminfo['prjid'] = result.data['prjid'];
      }
    })
  }

  inittoollist() {
    var url = '/lmtagFileTooldetail/list?standardcode='+this.id+'&tooltype=log&sort=seqnum&order=asc&_search=false&rows=-1&page=1&sidx=&sord=asc'
    this.httpService.get(url, null, result => {
      if (result.rows && result.rows.length > 0) {
        this.toollist = result.rows;
      }
    })
  }

  pageElementControl(data) {
    this.pageControl = data;
  }
  async pageElement(wfstatus, wfid) {// 初始化页面资源权限
    // var that = this;
    const data = await this.httpService.getPageControlElementAnsy("lmtagProjectLog_form", wfstatus, wfid);
    this.pageElementControl(data);
    var array = this.httpService.pageDate(data);
    this.formdata = this.httpService.getpagedata(this.formdata, array);
    this.formdata2 = this.httpService.getpagedata(this.formdata2, array);
    this.formdata3 = this.httpService.getpagedata(this.formdata3, array);
    this.formdata4 = this.httpService.getpagedata(this.formdata4, array);
  }

  planmore() {
    this.navCtrl.push('LmtagworkorderplanbaseinfoPage', {
      type: 'edit',
      prjid: this.prjid,
    })
    this.events.subscribe('workorderplanbase:forminfo', (data, time) => {
      this.projectinfo = data
      console.log('subscribe => ', this.projectinfo);
      this.events.unsubscribe('workorderplan:forminfo');
    })
  }

  // 选中项目
  chooseitem(event) {
    this.addlog(); // 预创建日志
    this.initprojectinfo(event.prjid);
  }

  showscanner() {
    this.navCtrl.push('ScannerPage', { 'type': 'lmtagProjectLog' }, { "animate": false });
  }

  // getUserName(id) {//根据id获取名称
  //   this.httpService.getUserName(id, res =>
  //     if (res.code == 0) {
  //       this.forminfo['creatername'] = res.data;
  //     }
  //   })
  // }

  // findProjectInfo() {
  //   this.httpService.get('/lmpjProject/findProjectMessage?id=' + this.projectid, null, data => {
  //     if (data.code == 0) {
  //       this.forminfo['projectname'] = data.data['projectname']
  //     }
  //   })
  // }

  changeToggle(value, forminfo, key) {
    if (value) {
      forminfo[key] = 1;
    } else {
      forminfo[key] = 0;
    }
    console.log(value, key, forminfo[key]);
  }
  // radiorightclick(forminfo, index, key) {
  //   if (forminfo[index][key] === true) {
  //     forminfo[index][key] = 1;
  //   } else if (forminfo[index][key] === false) {
  //     forminfo[index][key] = 0;
  //   }
  //   console.log(forminfo[index][key]);
  // }
  // changetoolready(value, key, index) {
  //   if (value) {
  //     this.forminfo[index][key] = 1;
  //   } else {
  //     this.forminfo[index][key] = 0;
  //   }
  //   console.log(this.forminfo[index][key]);
  // }

  // 数值加减按钮
  handleValueChange(operator, forminfo,  index) {
    if (operator == '+') {
      if (index == 'remain' && forminfo['num'] <= forminfo[index]) {
        return false;
      }
      forminfo[index]++
    } else if (operator == '-') {
      if (forminfo[index] == 0) {
        return false;
      }
      forminfo[index]--
    }
  }

  collapseClick(index) {//区块收缩/展开
    $('page-lmtagprojectlog #collapse' + index).toggle();
    $('page-lmtagprojectlog .collapse_show_icon' + index).toggle();
    $('page-lmtagprojectlog .collapse_hide_icon' + index).toggle();
  }

  submit() {
    if (this.prjid == null || this.prjid == '') {
      return this.toast.create({
        message: '请选择检修任务',
        duration: 1500,
        position: 'bottom'
      }).present();
    }
    // 入场
    if (this.forminfo['edittype'] == 1) {
      if (this.forminfo['enterperson'] == 0) {
        return this.toast.create({
          message: '入场检修人员人数不能为0',
          duration: 1500,
          position: 'bottom'
        }).present();
      }
      if (this.forminfo['enterpersonready'] != 1) {
        return this.toast.create({
          message: '请确认入场检修人员人数',
          duration: 1500,
          position: 'bottom'
        }).present();
      }
      if (this.toollist.length > 0 && this.toollist.findIndex(item => item.ready != 1) != -1) {
        return this.toast.create({
          message: '请确认入场工具数量',
          duration: 1500,
          position: 'bottom'
        }).present();
      }
    }
    // 出场
    if (this.forminfo['edittype'] == 2 && this.forminfo['leavepersonready'] != 1) {
      if (this.forminfo['leavepersonready'] != 1) {
        return this.toast.create({
          message: '请确认出场检修人员人数',
          duration: 1500,
          position: 'bottom'
        }).present();
      }
      if (this.toollist.length > 0 && this.toollist.findIndex(item => item.ready2 != 1) != -1) {
        return this.toast.create({
          message: '请确认出场工具数量',
          duration: 1500,
          position: 'bottom'
        }).present();
      }
    }

    this.httpService.initsavetime('entertime', this.forminfo);
    this.httpService.initsavetime('leavetime', this.forminfo);

    this.forminfo['prjid'] = this.prjid;

    this.toollist.map(item => {
      item.ready = +item.ready
      item.ready2 = +item.ready2
    })

    let obj = {
      lmtagFileTooldetailService: this.toollist,
      master: this.forminfo
    }
    this.httpService.post('/lmtag/lmtagProjectLogService/saveMasterSlave', obj, result => {
      if (result.code == 0) {
        this.toast.create({
          message: '保存成功',
          duration: 1500,
          position: 'bottom'
        }).present();

        // this.initinfo();
        // this.inittoollist();
        // this.initprojectinfo(this.forminfo['prjid']);
        this.navCtrl.pop().then(() => {
          this.events.publish('projectlogCall', null);
        });
      } else {
        this.toast.create({
          message: '保存失败',
          duration: 1500,
          position: 'bottom'
        }).present();
      }
    })
  }

  removelog() {
    this.httpService.removeTableIds('lmtagProjectLog', { ids: this.forminfo['logid'] }, res => {
      if (res.code == 0) {
        this.toastCtrl.create({
          message: '删除成功',
          duration: 1200,
          position: 'top'
        }).present()
        this.navCtrl.pop().then(() => {
          this.events.publish('projectlogCall', null);
        });
      }
    }, this.httpService.CONTENTTYPE_FORMURLENCODE)
  }
}



